import { fromEvent } from 'rxjs';
import { scan, merge } from 'rxjs/operators';
import {partial, defaults, forEach, random, findIndex, range} from 'lodash-es';

function norm2(x,y) {
  return x*x + y*y;
}
function dist2(x1,y1,x2,y2) {
  return norm2(x1-x2, y1-y2);
}
function minus(p1, p2) {
  return (p1[0] - p2[0], p1[1] - p2[1])
}
function plus(p1, p2) {
  return (p1[0] + p2[0], p1[1] + p2[1])
}

function process_mouse(state, evt) {
  switch (expr) {
    case 'down':
    var chosen_circle = findIndex(state.circles, circle => function(circle) {
      return (evt.offsetX, evt.offsetY, circle[0], circle[1]) < circle[2]*circle[2];
    });
    if (chosen_circle) {
      state.chosen_circle = chosen_circle;
      state.chosen_at = [evt.offsetX, evt.offsetY];
    }
    return state;
    case 'move':
    if (state.chosen_circle) {
      var circ = state.circles[state.chosen_circle]
      var offset = plus([circ.x, circ.y],
        minus([evt.offsetX, evt.offsetY], state.chosen_at))
      state.circles[state.chosen_circle] = circ;
    }
    return state;
    case 'up':
    state.chosen_circle = null;
    state.chosen_at = null;
    default:
    return state;
  }
}
function render(ctx, state) {
    forEach(state.circles, circle => function(circle) {
      ctx.beginPath();
      ctx.arc(circle.x, circle.y, 10, 0, 2 * Math.PI);
      ctx.fillStyle= 'red'
      ctx.fill();
    });
}

function init() {
    const canvas = document.getElementById('canvas');
    const ctx = canvas.getContext('2d');
    const down = fromEvent(document, 'mousedown').pipe(e=>defaults({'type': 'down'}, e));
    const move = fromEvent(document, 'mousemove').pipe(e=>defaults({'type': 'move'}, e));
    const up = fromEvent(document, 'mouseup').pipe(e=>defaults({'type': 'up'}, e));
    const mouse = merge(
          down,
          move,
          up
        );
    const initial_state = {
      'circles': forEach(range(0,10), x=>[random(10,590,true),random(100,500,true),10])
    }
    const mouse_observer = mouse.pipe(scan(process_mouse))
    mouse_observer.subscribe()
}

init();
